.signin-panel {
    z-index: 2;
    position: absolute;
    top: calc((100% - 400px)/2);
    left: calc((100% - 500px)/2);
    width: 500px;
    height: 400px;
    background-color: white;
    text-align: center;
    padding: 20px 50px 50px 50px;
    border-radius: 5px;
    
 }
 
 .signin-panel h5 {
     color: #eb8334;
 }
 
 .btn {
     margin-top: 30px;
     width: 100%;
 }
 .input-field {
     margin-top: 30px;
     margin-bottom: 0px;
    
 }

 .back {
     margin-top: 0px !important;
     width: 30px !important;
     height: 30px;
     border-radius: 15px;
     border: none;
     color: rgb(168, 160, 160);
     background-color: white;
     border: 1px solid rgb(168, 160, 160);
     display: block;
    
 }